<template>
  <div class="header">
    <el-row type="flex" align="middle">
      <el-col id="header-logo" :span="4">
        <img src="https://g-lab.xyz/images/og_image_g_lab.png" alt="车库Lab-分享最新最特别的小技能" height="28">
      </el-col>
      <el-col class="navbar navbar-start" :span="4">
        <el-menu :default-active="this.$route.path.substr(1)" mode="horizontal" @select="handleSelect" active-text-color="#909399">
          <el-menu-item index="home">{{ $t('navbar.home') }}</el-menu-item>
          <el-menu-item index="blog">{{ $t('navbar.blog') }}</el-menu-item>
        </el-menu>
      </el-col>
      <el-col class="navbar navbar-end" :span="4" :push="12">
        <el-dropdown @command="handleSelectLanguage">
          <span class="el-dropdown-link">
            {{ $t('navbar.language') }}<i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="cn">中文</el-dropdown-item>
            <el-dropdown-item command="en">English</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'Header',
  methods: {
    handleSelect(key) {
      if (key === 'blog') {
        window.location.href = 'https://g-lab.xyz'
      } else {
        this.$router.push({ name: 'index' })
      }
    },
    handleSelectLanguage(command) {
      this.$i18n.locale = command
      localStorage.lang = command
    }
  }
}
</script>

